.home-page {
    height: calc(100vh - 45px); 
    background-image: url('../assets/homehotel.jpg'); 
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    overflow: auto;
}
  
.content {
    position: relative;
    z-index: 1;
    text-align: center;
    color: white;
    padding-top: 45px; 
}

.centered-box {
    position: absolute;
    top: 1000%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(0, 0, 0, 0.7); 
    padding: 40px; 
    border-radius: 20px; 
    width: 80%; 
    max-width: 600px; 
    text-align: center;
}

.centered-box h2 {
    color: #ff9800; 
    font-family: 'Arial Black', sans-serif; 
    font-size: 2.5rem; 
    margin-bottom: 20px; 
}

