.filters {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    z-index: 100;
    margin-left: 20px;
    margin-right: 20px;
}

.filters input select {
    padding: 10px;
    border: none;
    border-radius: 5px;
    background-color: rgba(255, 255, 255, 0.8);
    outline: none;
}

.booking-table {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    z-index: 100;
    margin-left: 20px;
    margin-right: 20px;
}

.booking-table table {
    border-collapse: collapse;
    width: 100%;
}

.booking-table th, .booking-table td {
    padding: 8px;
    border: 1px solid #ddd;
    text-align: left;
}

.booking-table th {
    background-color: #f2f2f2;
    color: #333;
}

.booking-table tr{
    background-color:white;
    color: grey;
}

