* {
    box-sizing: border-box;
}

body{
    margin: 0;
}

.container{
    text-align: center;
}

.nav{
    background-color: #333;
    color: white;
    gap: 2rem;
    padding: 0 1rem;
}

.nav ul{
    padding: 0;
    margin: 0;
    list-style: None;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
}

.nav a{
    color: inherit;
    text-decoration: None;
    height: 100%;
    display: flex;
    align-items: center;
    padding: .25rem;
    font-size: 2rem;
}

.nav li.active{
    background-color: white;
    color: #333;
}

.nav li:hover{
    background-color: white;
    color: #333;
}

